<template>
  <div>
    <h1>ÎÇ¥Í∞Ä Ï¢ãÏïÑÌïòÎäî ÏòÅÌôîÎì§ üéû</h1>
    <div class="deleteAll">
      <button @click="deleteAllList">Ï†ÑÏ≤¥ÏÇ≠Ï†ú</button>
    </div>
    <MyMovieCard  @deletemine="removeMyList" />
  </div>
</template>

<script>
import MyMovieCard from "../components/MyMovieCard.vue";
import {mapGetters} from 'vuex';
export default {
  methods: {
    deleteAllList() {
      if (
        confirm(" Ï†ÑÏ≤¥ÏÇ≠Ï†ú ÌïòÏãúÍ≤†ÏäµÎãàÍπå? ÌôïÏù∏ÏùÑ ÎàÑÎ•¥ÏãúÎ©¥ Î≥µÍµ¨ÎêòÏßÄ ÏïäÏäµÎãàÎã§. ")
      ) {
        localStorage.removeItem("store");
        location.reload();
      }
    },
    removeMyList(discard) {
      for (let i = 0; i < this.myMovie.length; i++) {
        if (discard === this.myMovie[i].id) {
          this.myMovie.splice(i, 1);
          i--;
        }
      }
      localStorage.setItem("store", JSON.stringify(this.myMovie));
    },
  },
  components: {
    MyMovieCard,
  },
  computed:{
    ...mapGetters(['myMovie'])
  },
  mounted(){
    this.$store.commit('loadMovie');
  }
};
</script>

